.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #08415C;
  height: 165x;
  padding: 2px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
  padding: 2px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body{
	background-color: #F1BF98;
	
}

        .navWide {
            display: block;
			background-color: #CC2936;
			overflow: hidden;
        }
    

.navWide a {
    float: left;
    color: #EEE5E9;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

/* Change the color of links on hover */
.navWide a:hover {
    background-color: #F1BF98;
    color: #CC2936;
}

/* Add a color to the active/current link */
.navWide a.active {
    background-color: #CC2936;
    color: #6B818C;
}

button {
    background-color: #CC2936; 
    border: none;
	border-radius: 7px;
    color: white;
    padding: 20px 50px;
	margin-left: 10px;
	margin-top: 5% ;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
	opacity: 0.6;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
}

button:hover{
opacity: 1;

}

.heading{
margin-top: 5%;

}



